import React from 'react'
import Alarmbell from "../audio/music.mp3"

const Alarm = () => {
    let audio = new Audio();
    audio.src = Alarmbell;
    const  ringBell=()=> {
        audio.play();
    }

    const setAlarm =(e)=> {
        e.preventDefault();
        const alarm = document.getElementById('alarm');
        const alarmDate = new Date(alarm.value);
        const now = new Date();
        let timeToAlarm = alarmDate - now;
        console.log(timeToAlarm);
        if (timeToAlarm >= 0) {
            setTimeout(() => {
                ringBell();
            }, timeToAlarm);
        }
    }
    return (
        <>
            <form>
                <div className="form-group">
                    <label htmlFor="alarm">Set Alarm</label>
                    <input autoComplete="off" type="datetime-local" className="form-control" id="alarm" name="alarm"
                        placeholder="Enter the time in yyyy-mm-dd hh:mm:ss" />
                </div>
                <button onClick={setAlarm} id="alarmSubmit" type="submit" className="btn btn-danger">Set Alarm</button>
            </form>
        </>
    )
}

export default Alarm
